import React, { Component } from 'react';
import $ from 'jquery';
import MenuComponents from './components/Menu/MenuComponents';
import Footer from './components/Footer/FooterComponents';
import { Router, Route, BrowserHistory, IndexRoute, NavLink, Switch, } from 'react-router';

class App extends Component {

	componentWillMount() {
		
		 $(function(){
			 var nom = window.location.pathname;
			 var x =nom;
			 nom = nom.split("/");
			 nom = nom[nom.length - 1];
			 nom = nom.substr(0, nom.lastIndexOf("."));
			 nom = nom.replace(new RegExp("(%20|_|-)", "g"), "");
 
			 alert(x);

			 if(nom !== '/') {
				 $("nav").addClass("red");
			 } else {
				 $("nav").removeClass("red")
			 }
		 
		 })
	 }
	render() {
		return (
			<div className="App Site" id="wrapper">
				<div className="Site-content">
					<MenuComponents />
				</div>
				<div>
				<footer className='site-footer mtop'>
					<div className='wrapper'>
						<div className='footer-social'>
							<ul>
								<li>
									<Footer/>
								</li>
							</ul>
						</div>
					</div>
					<div className='identity'>
						<p> 2018 Maxtanie Petit Dol</p>
					</div>
				</footer>
			</div>
			</div>
		);
	}
}
export default App;


