import React, { Component } from 'react';
import {
  HashRouter as Router,
  Route,
  NavLink,
  Switch
  
} from 'react-router-dom';

import BrowserHistory from 'react-router';

import AccueilComponents from '../Accueil/AccueilComponents';
import CompetencesComponents from '../Competences/CompetencesComponents';
import PortfolioComponents from '../Portfolio/PortfolioComponents'
import CentresInteretComponents from '../CentresInteret/CentresInteretComponents'
import ParcoursComponents from '../Parcours/ParcoursComponents';
import CvComponents from '../Cv/CvComponents';
import ContactComponents from '../Contact/ContactComponents';



class MenuComponents extends Component {
    render() {
        return(
            <Router history={BrowserHistory}>
            <div>
                <nav>
                    <div className="container">
                        <div className="flex">
                            <NavLink to="/" className="brand-logo">Logo</NavLink>
                            <ul className="flex">
                                <li><NavLink to="/competences">Comptences</NavLink></li>
                                <li><NavLink to="/portfolio">Portfolio</NavLink></li>
                                <li><NavLink to="/hobbies">Centres d'intrt</NavLink></li>
                                <li><NavLink to="/parcours">Parcours</NavLink></li>
                                <li><NavLink to="/cv">CV</NavLink></li>
                                <li><NavLink to="/contact">Contact</NavLink></li>
                            </ul>
                        </div>
                    </div>          
                </nav>
               <Switch>
                <Route exact path="/" component={AccueilComponents}/>
                <Route path="/competences" component={CompetencesComponents}/>
                <Route path="/portfolio" component={PortfolioComponents}/>
                <Route path="/hobbies" component={CentresInteretComponents}/>
                <Route path="/parcours" component={ParcoursComponents}/>
                <Route path="/cv" component={CvComponents}/>
                <Route path="/contact" component={ContactComponents}/>  
                </Switch>             
            </div>          
        </Router>
        );
    }
}

export default MenuComponents


