import React, { Component } from 'react';
import Anime from 'react-anime';
import ScrollAnimation from 'react-animate-on-scroll';
import Typist from 'react-typist';
import Background from '../../../images/HeaderPresentation/BackgroundHeaderPresentation.png'


var sectionStyle = {
    //backgroundImage: `url(${Background})`,
    backgroundImage: "url(https://www.digitalplatforms.co.za/wp-content/uploads/2015/11/Website-Design-Background.png)",
    backgroundRepeat: "no-repeat",
    backgroundPosition: "center",
    backgroundSize: "cover"
}
class AccueilComponents extends Component {
    render() {
        return(
            <div>
            <ScrollAnimation  animateIn='bounceInRight'>
                <header className="headerPresentation" style={sectionStyle}>
                    <div className="info">
                        <div className="titleJob">
                            <h1>Dveloppeuse front-end </h1>
                        </div>
                        <div className="textPresentation">
                            <Typist>
                                <p>Je mappelle <span className="green">Maxtanie Petit Dol</span> je suis age de <span className="green">21 ans</span> et passione par cet univers<br/> 
                                binaire o la <span className="green">cration et limmagination</span> na aucune limite.</p>                            
                            </Typist>
                        </div>
                        <div className="downloadCv">
                            <a href="#">Download Resume</a>
                        </div>
                    </div>                           
                </header>
                </ScrollAnimation>
            </div>
            
        )
    }
}

export default  AccueilComponents;


